  position: fixed;
}

.meteor {
  width: 100%;
  height: 100%;
}

.blackhole {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
}


canvas {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  margin: auto;
}

.centerHover {
  position: absolute;
  left: 50%;
