  transform: translate(-50%, -50%);
  z-index: 1030;
  width: 25%;
  height: 25%;
  color: white;
  text-align: center;
  font-weight: 600;
}

.custom-logo {
  max-width: 150px;
  margin-left: 20px;
  margin-top: 10px;
}

.meteor-text {
  width: 100%;
  position: absolute;
  bottom: 25%;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  text-align: center;
  font-weight: 600;
}

@media only screen and (min-width: 768px) {
  body {
    background-image: url(https://storage.googleapis.com/cardano-nft/cardano-warriors-whitepaper/space_1920x1080.gif);
    background-repeat: no-repeat;
    background-size: cover;
  }

  .meteor-text {
    font-size: 1.6rem;
  }

  .unity-container {
    width: 100%;
    height: 100%;
    position: absolute;
    
    /* transform: translate(-50%, -50%); */
    min-height: 100vh;
    max-height: 100vh;
    animation: createBox 1s;
  }
    @keyframes createBox {
    from {
      transform: scale(0);
    }
    to {
      transform: scale(1);
    }
  }
}

@media only screen and (max-width: 768px) {
